body{
    position: relative;
    margin: 40px;
    padding: 15px;
    padding-top: 20px;
    padding-bottom: 40px;
    /* border: solid 5px grey; */
    min-height: 400px;
    font-family: 'Times New Roman', Times, serif;
    background: #f1edeb;
}
h1 {
    text-align: center;
}
input {
    font-family: monospace;
    border: none;
    border-bottom: 1px solid grey;
    margin: 5px;
}
#quote-submit{
    width: 33px;
    padding: 2px;
    border-radius: 25%;
}
button {
    border: none;
    border-bottom: 1px solid grey;
    margin: 5px;
    font-family: monospace;
    font-size: 13px;
}
.container {
    max-width: 600px;
    background: white;
    padding :20px;
}
.quote-input{
    font-size: 20px;
    font-weight: lighter;
    text-decoration: underline grey;
    width: 100%;
    min-height: 100px;
    border: 1px solid grey;
    margin-bottom: 10px;
    padding: 5px;
}
.share-quotes-label{
    font-family: monospace;
}
.input-detail{
    margin-left: 15vh;
}
#author-input{
    padding: 0px;
    width: 100px;
}
#title-input{
    padding: 0px;
    width: 250px;
}
#page-input{
    padding: 0px;
    width: 45px;
}
#comment-input{
    padding: 0px;
    width: 250px;
}
.input-personal {

    margin-left: 15vh;
}

ul {
    list-style: none;
    font-size: 20px;
}

.quotes-list{
    position: relative;
    border: 2px solid #f1edeb;
    margin-top: 35px;
    margin-left: 20px;
    padding: 10px;
    width: 100%;
    /* background: rgb(205, 205, 205); */
}
.profile-quote-list{
    position: relative;
    margin-top: 20px;
    width: 100%;
}
.profile-quote-list ul{
    padding-left: 0px;
}
.profile-quote-list li{
    margin-top: 30px;
}
#created-at {
    position: absolute;
    right: 20px;
    top: 40px;
}
.profile-created-at {
    position: absolute;
    right: 20px;
    top: 5px;
    font-size: 10px;
}

.quote-content{
    padding: 20px;
    color: black;
    font-size: 15px;
    font-weight: lighter;
    font-family: monospace;
    text-decoration: underline grey;
    background: white;
}
.profile-quote-content{
    padding-top: 40px;
    padding: 20px;
    color: black;
    font-size: 15px;
    font-weight: lighter;
    font-family: monospace;
    text-decoration: underline grey;
    background: white;
}

.quote-origin{
    color: rgb(38, 38, 38);
    font-size: 13px;
    font-family: serif;
    text-align: right;
}
.profile-quote-origin{
    color: rgb(38, 38, 38);
    font-size: 13px;
    font-family: serif;
    text-align: right;
}
.quote-origin p{
    margin-top: 3px;
    margin-bottom: 0px;
    font-family: monospace;
}
.profile-quote-origin p{
    margin-top: 3px;
    margin-bottom: 0px;
    margin-right: 5px;
    font-family: monospace;
}
.profile-quote-author{
    margin-bottom: 0px;
}
#quote-author{
    margin-bottom: 0px;
}
.quote-edit {
    font-size: 15px;
    font-weight: lighter;
    font-family: monospace;
    text-decoration: rgb(126, 14, 126) underline;
    border: 1px solid;
    padding: 10px;
    background: white;
}
.comment-section p {
    margin-bottom:14px;
    margin-left: 14px;
    font-size: 15px;
}
.quote-buttons{
    text-align: right;
}
/* navigation */
.navigation{
    font-family: Georgia, 'Times New Roman', Times, serif
}
.link {
    color: grey;    
}
.link :hover{
    color: grey;
    text-decoration: none;
}
.navigation :hover{
    color: grey;
    font-weight: bolder;
}
#fav-author-list{
    padding-left: 15px;
}
.fav-authors {
    padding-bottom: 10px;
}
.profile-fav-author ul{
    font-size: 14px;
    padding: 0px;
    margin-bottom: 2px;
    list-style-type: square;
}
.profile-edit{
    margin-left: 0px;
}
.box {
    display: flex;
    flex-direction: column;
    border: 1px solid #f1edeb; 
    padding: 10px;
}
.landing {
    display: flex;
    flex-direction: row;
    align-items: baseline;
}
.landingpage{
    padding-left: 20px;
    margin-right: 35px;
}
.auth-form-2 input{
    margin-left: 0px;
}

.profile-fav-author{
    flex: 1 1 auto;
}
.profile-quotes{
    flex: 3 1 auto;
    border-top: 1px solid #f1edeb; 
    padding-top: 10px;
     position: relative;
}
.profile-quotes li{
    position: relative;
    width: 100%;
    border: 1px solid #f1edeb; 
}

/*user profile*/
.user-name{
 text-align: center;
 text-decoration: underline;
 margin-bottom: 25px;
}
